import { useState } from "react";
import ProfileMenu from "./modals/profileMenu";
import siteLogo from '../assets/siteLogo.png';
import profileLogo from '../assets/profileImage.png';
import { useLocation, useNavigate } from "react-router-dom";
import { LOGIN_ROUTE, MAIN_ROUTE, PROFILE_ROUTE, REGISTRATION_ROUTE } from "../routes/consts";

const Header = () => {

    const [modal, setModal] = useState(false);

    const location = useLocation();
    const navigate = useNavigate();
    const isMainPage = location.pathname === MAIN_ROUTE;

    // const user = {
    //     name: 'Denis',
    //     email: 'Den-step@mail.ru',
    //     password: 'hola123',
    //     isAuth: true
    // }
    const [isAuth, setIsAuth] = useState(true);


    const openModal = () => {
        if (!modal) {
            setModal(true);
        } else {
            setModal(false);
        }
    }

    const openMobileModal = () => {
        openModal();
    }

    const navigateUser = (e, name) => {
        e.preventDefault();

        switch (name) {
            case '':
                navigate(LOGIN_ROUTE);
                openModal();
                break;
            case '':
                navigate(REGISTRATION_ROUTE);
                openModal();
                break;
            case '':
                console.log(' PROFILE')
                navigate(PROFILE_ROUTE);
                openModal();
            default:
                navigate(MAIN_ROUTE)
                openModal();
        }

        
    }

    return (
        <div className="wrap header wrap--header wrap-header--main">
            <a onClick={() => navigate(MAIN_ROUTE)} className="header__link"><img src={siteLogo} alt=" " className="header__logo"/></a>
            <nav className="navigation header__navigation">
                <ul className="navigation__list">
                    {isMainPage ? 
                        <>
                            <a onClick={() => navigate(MAIN_ROUTE)} className="navigation__link"><li className="navigation__item"></li></a>
                            <a href="#programs" className="navigation__link"><li className="navigation__item"></li></a>
                            <a href="#about" className="navigation__link"><li className="navigation__item"> </li></a>
                            <a href="#education" className="navigation__link"><li className="navigation__item"></li></a>
                            <a href="#contacts" className="navigation__link"><li className="navigation__item"></li></a>
                        </>
                        :
                        <>
                            <a onClick={() => navigate(MAIN_ROUTE)} className="navigation__link"><li className="navigation__item"></li></a>
                            <a onClick={() => navigate(MAIN_ROUTE)} className="navigation__link"><li className="navigation__item"></li></a>
                            <a onClick={() => navigate(MAIN_ROUTE)} className="navigation__link"><li className="navigation__item"> </li></a>
                            <a onClick={() => navigate(MAIN_ROUTE)} className="navigation__link"><li className="navigation__item"></li></a>
                            <a onClick={() => navigate(MAIN_ROUTE)} className="navigation__link"><li className="navigation__item"></li></a>
                        </>
                    }
                </ul>    
            </nav>
            <div className="profile profile-container">
                <button className="profile__button" onClick={openModal}>
                    <img className="profile__logo" src={profileLogo} alt=" "/>
                    <div className="profile__logo--mobile-container">
                        <hr className="profile__logo--mobile" />
                        <hr className="profile__logo--mobile" />
                        <hr className="profile__logo--mobile" />
                    </div>
                </button>
                <ProfileMenu visible={modal} setVisible={setModal}>
                    <ol className="list header-list">
                        {
                            isAuth ?
                                <>
                                    <li onClick={() => {navigate(PROFILE_ROUTE); openModal()}} className="list__item header-list__item"> </li>
                                </>
                                :
                                <>
                                    <li onClick={(e) => navigateUser(e, '')} className="list__item header-list__item"></li>
                                    <li onClick={(e) => navigateUser(e, '')} className="list__item header-list__item"></li>
                                </>
                        }
                    </ol>

                    <ol className="list header-list header-list--mobile wrap">
                        <button className='button-exit button-exit--menu' onClick={openMobileModal}>
                                <div className='button-exit__left'></div>
                                <div className='button-exit__right'></div>
                        </button>
                        {
                            isMainPage ? 
                            <>
                                <a onClick={(e) => navigateUser(e)} className="link"><li className="list__item header-list__item"></li></a>
                                <a onClick={openMobileModal}className="link" href="#programs"><li className="list__item header-list__item"></li></a>
                                <a onClick={openMobileModal}className="link" href="#about"><li className="list__item header-list__item"> </li></a>
                                <a onClick={openMobileModal}className="link" href="#education"><li className="list__item header-list__item"></li></a>
                                <a onClick={openMobileModal}className="link" href="#contacts"><li className="list__item header-list__item"></li></a>
                                <a onClick={(e) => navigateUser(e, '')} className="link"><li className="list__item header-list__item">/</li></a>
                            </>
                            :
                            <>
                                <a onClick={(e) => navigateUser(e)} className="link"><li className="list__item header-list__item"></li></a>
                                <a onClick={(e) => navigateUser(e)} className="link"><li className="list__item header-list__item"></li></a>
                                <a onClick={(e) => navigateUser(e)} className="link"><li className="list__item header-list__item"> </li></a>
                                <a onClick={(e) => navigateUser(e)} className="link"><li className="list__item header-list__item"></li></a>
                                <a onClick={(e) => navigateUser(e)} className="link"><li className="list__item header-list__item"></li></a>
                                <a onClick={(e) => navigateUser(e, '')} className="link"><li className="list__item header-list__item">/</li></a>
                            </>
                        }
                    </ol>
                </ProfileMenu>
            </div>
        </div>
    );
}

export default Header;