import {createSlice} from "@reduxjs/toolkit";

const shadingReducer = createSlice({
    name: 'shading',
    initialState: {
        shading: false,
    },
    reducers: {
        addShading(state, action) {
            console.log(state, "state - addShading");
            console.log(action, "action - addShading");
            state.shading = action.payload;
        },
        removeShading(state, action) {
            console.log(state, "state - removeShading");
            console.log(action, "action - removeShading");
            state.shading = action.payload;
        }
    }
})

export const {addShading, removeShading} = shadingReducer.actions;

export default shadingReducer.reducer;