import React, { useEffect, useState } from 'react';
import image1 from '../assets/image_1.png';
import image2 from '../assets/image_2.png';
import image3 from '../assets/image_3.png';
import ProgramModal from './modals/programModal';

const Slider = () => {

  const [modal, setModal] = useState(false);

  const openModal = () => {
    console.log('modal', modal);
    if (!modal) {
        setModal(true);
    } else {
        setModal(false);
    }
}

    useEffect(() => {
        const link = document.createElement('link');
        link.rel = 'stylesheet';
        link.href = 'https://cdnjs.cloudflare.com/ajax/libs/Swiper/4.3.5/css/swiper.min.css';
        document.head.appendChild(link);
    
        const script1 = document.createElement('script');
        script1.src = 'https://cdnjs.cloudflare.com/ajax/libs/Swiper/4.3.5/js/swiper.min.js';
        script1.async = true;
        document.body.appendChild(script1);
    
        script1.onload = () => {
          const script2 = document.createElement('script');
          script2.textContent = `
            var swiper = new Swiper('.blog-slider', {
              spaceBetween: 30,
              effect: 'fade',
              loop: true,
              mousewheel: {
                invert: false,
              },
              pagination: {
                el: '.blog-slider__pagination',
                clickable: true,
              }
            });
          `;
          document.body.appendChild(script2);
        };
    
        return () => {
          document.head.removeChild(link);
          document.body.removeChild(script1);
        };
      }, []);


  return (
    <>
    <>
    <ProgramModal visible={modal} method={openModal} setVisible={setModal} />

    </>
    <div className='blog'>
    <div className="blog-slider">
      <div className="blog-slider__wrp swiper-wrapper">
        <div className="blog-slider__item swiper-slide">
          <div className="blog-slider__img">
            <img src={image1} alt="" />
          </div>
          <div className="blog-slider__content">
            <div className="blog-slider__title"></div>
            <div className="blog-slider__text">    ,     ,          3   </div>
            <a onClick={openModal} href="#programitem" className="blog-slider__button"></a>
            {/* <ProgramModal visible={modal} method={openModal} setVisible={setModal} /> */}
          
          </div>
        </div>

        <div className="blog-slider__item swiper-slide">
          <div className="blog-slider__img">
            <img src={image2} alt="" />
          </div>
          <div className="blog-slider__content">
            <div className="blog-slider__title"></div>
            <div className="blog-slider__text">    ,     ,          3   </div>
            <a onClick={openModal} href="#programitem" className="blog-slider__button"></a>
            {/* <ProgramModal visible={modal} method={openModal} setVisible={setModal} /> */}
          
          </div>
        </div>
        <div className="blog-slider__item swiper-slide">
          <div className="blog-slider__img">
            <img src={image3} alt="" />
          </div>
          <div className="blog-slider__content">
            <div className="blog-slider__title"></div>
            <div className="blog-slider__text">    ,     ,          3   </div>
            <a onClick={openModal} href="#programitem" className="blog-slider__button"></a>
            {/* <ProgramModal visible={modal} method={openModal} setVisible={setModal} /> */}
          </div>
        </div>
      </div>

      <div className="blog-slider__pagination link"></div>
      {/* <ProgramModal visible={modal} method={openModal} setVisible={setModal} /> */}
    </div>
    </div>
    </>
  );
};

export default Slider;
