import React, { useState } from "react";
import { useLocation, useNavigate } from "react-router-dom";
import { LOGIN_ROUTE, MAIN_ROUTE, REGISTRATION_ROUTE } from "../routes/consts";
import headerMainLeftLine from "../assets/headerMainLeftLine.png";

const AuthPage = () => {


    const location = useLocation();
    const navigate = useNavigate();
    const isLogin = location.pathname === LOGIN_ROUTE;

    const [name, setName] = useState('');
    const [lastName, setLastName] = useState('');
    const [email, setEmail] = useState('');
    const [phone, setPhone] = useState('');
    const [password, setPassword] = useState('');

    const submitForm = async (e) => {
        // let data = null;
        e.preventDefault();

        console.log({name: name, lastName: lastName, email: email, phone: phone, password: password});

        try {
            if (isLogin) {
                // data = await login(email, password);
                // console.log(data);
            } else {
                // data = await registration(email, password);
                // console.log(data);
            }
    
            navigate(MAIN_ROUTE);
        } catch (e) {
            alert(e.response.data.message);
        }
    }

    return (
        <form onSubmit={submitForm} className="wrap auth">
            <div className="wrap-auth">
                {
                    isLogin ? 
                        <>
                            <h1 className="auth__title"></h1>
                            <input type="email" required className="auth__input" value={email} onChange={(e) => setEmail(e.target.value)} placeholder='Email' />
                            <input type="password" required className="auth__input" value={password} onChange={(e) => setPassword(e.target.value)} placeholder='' />
                            <div className="auth__checkbox-group">
                                <input className="auth__checkbox" required type="checkbox" name="agreement" value="politics" />
                                <div></div>
                                <label className="auth__checkbox-label" for="politics"> </label>
                            </div>
                            <a className="link link--bad-password"> </a>
                            <div className="auth__bottom-section">
                                <img className="auth__line" alt='   ' src={headerMainLeftLine} />
                                <button className="button auth__submit-button button auth__submit-button--login" type="submit"></button>
                            </div>
                            <span className="link link--registration auth__checkbox-text" onClick={() => navigate(REGISTRATION_ROUTE)}></span>
                        </>
                        :
                        <>
                            <h1 className="auth__title"></h1>
                            <input type="text" required className="auth__input" value={name} onChange={(e) => setName(e.target.value)} placeholder='' />
                            <input type="text" required className="auth__input" value={lastName} onChange={(e) => setLastName(e.target.value)} placeholder='' />
                            <input type="email" required className="auth__input" value={email} onChange={(e) => setEmail(e.target.value)} placeholder='Email' />
                            <input type="number" required className="auth__input" value={phone} onChange={(e) => setPhone(e.target.value)} placeholder=' ' />
                            <input type="password" required className="auth__input" value={password} onChange={(e) => setPassword(e.target.value)} placeholder='' />
                            <div className="auth__checkbox-group">
                                <input className="auth__checkbox" required type="checkbox" name="agreement" value="politics" />
                                {/* <div></div> */}
                                <label className="auth__checkbox-label" for="politics">   <a className="auth__link link" href="#">   </a></label>
                            </div>
                            <div className="auth__checkbox-group">
                                <input className="auth__checkbox" required type="checkbox" name="agreement" value="politics" />
                                {/* <div></div> */}
                                <label className="auth__checkbox-label" for="politics">   <a className="auth__link link" href="#"></a></label>
                            </div>
                            <div className="auth__bottom-section">
                                <img className="auth__line" alt='   ' src={headerMainLeftLine} />
                                <button className="button auth__submit-button button" type="submit"></button>
                            </div>
                        </>
                }
            </div>
        </form>
    );
};

export default AuthPage;